import React  from 'react'

const Newsitem =(props)=> {
  
      let{title, description, imageUrl, newsUrl, author, date, source }=props;
    return (
      <div className="my-3 mx-4">

        <div className="card" >
        <img src={!imageUrl?"https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQot37KIrwFt7JqB4TnKIlAAdNYGIFBCmLfsw&usqp=CAU":imageUrl} className="card-img-top" alt="..."/>
        <div className="card-body">
          <h5 className="card-title">{title}</h5>
          <p className="card-text">{description}</p>
          <p className="card-text"><small className="text-muted">Source: {source}</small></p>
          <p className="card-text"><small className="text-muted">By {!author?"Unknown":author} on {new Date(date).toGMTString()}</small></p>
          <a href={newsUrl} target="_blank" rel="noreferrer" className="btn btn-dark">Read more</a>
        </div>
      </div>
      </div>
    )
  }


export default Newsitem
